.search-results {
    padding: 0;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}

.results {
    list-style: none;
    margin-bottom: 2rem;
}

.pagination {
    padding: 1rem;
    &::after {
        content: "";
        display: table;
        clear: both;
    }

    &__btn {
        &--prev {
            float: left;
        }
        &--next {
            float: right;
        }
    }
}

.copyright {
    color: $color-grey-dark-2;
    font-size: 1.2rem;
    padding: 0 3.5rem;
    margin-top: 4rem;

    .twitter-link:link,
    .twitter-link:visited {
        color: $color-grey-dark-2;
    }
}
